import { useState, useEffect } from "react";
import axios from "axios";

const Index = (props: any) => {
  const [clients, setClient] = useState([{_id:"ff", name:"ffff"}]);

  useEffect(() => {
    return setClient(props.data);
  })

  const mapClients = (cl: any) => {
    if (cl !== []) {
      const listOfClients = cl.map(e => {
        <li key={e._id}>{e.name}</li>;
      });
      return listOfClients;
    }
  };

  return (
    <div>
      <h1>hiii</h1>
      {console.log(clients)}
      <ul>{mapClients(clients)}</ul>
    </div>
  );
};

Index.getInitialProps = async () => {
  const res = await axios.get("http://localhost:8080/api/clients");
  const data = await res.data;
  return {
    data
  };
};

export default Index;
